import React from 'react';
import $ from 'jquery';
import '../styles/header.scss';

const bgImages = [
    "https://miro.medium.com/max/1200/1*cY8vEbWdbbc0ZyPDdRuCIg.jpeg",
    "https://guiauniversitaria.mx/wp-content/uploads/2021/03/5-caracteristicas-de-una-mujer-empoderada.jpg",
    "http://www.nosotras13.cl/sites/default/files/styles/manualcrop_740x415/public/mujer13/field-image/4905246.jpg?h=e2df536c&itok=M0lpjDcW"
]

$(document).ready(() => {
    $('#header-title-container').fadeIn(2000);
});

class Header extends React.Component {
    constructor(props) {
        super(props);
        this.state = {};
    }

    render() {
        return(
            <div id='home'>
                <div className='blur'>
                    <div id='header-title-container'>
                        <h1>Milagro Rengifo</h1>
                        <h2>Abogada penalista especializada en derechos de la mujer</h2>
                    </div>
                    <div className='share-buttons'>
                        <Links />
                    </div>
                </div>
            </div>
        );
    }
}

const Links = () => {
    let linkClass = [
        ['fa-facebook-f','facebook'],
        ['fa-instagram','instagram'],
        ['fa-youtube','youtube'],
        ['fa-twitter','twitter'],
        ['fa-linkedin-in','linkedin']
    ];

    const links = linkClass.map((x) => {
        return(
            <a
                className={'share-button fa-brands ' + x[0]}
                id={x[1] + '-button'}
                href='#'
            />
        );
    });
    return(links);
}

export default Header;