class Comments extends React.Component {
  constructor(props) {
    super(props);
    this.state = {};
  }

  render() {
    return (
      <section className="main-section" id="comments">
        <div className="comment-container">
            <Carousel
              showArrows={true}
              infiniteLoop={true}
              showThumbs={false}
              showStatus={false}
              autoPlay={true}
              interval={6100}
            >
              {comments.comments.map((x) => {
                return (
                  <div className="myCarousel" key={'comment-id-' + x.id}>
                  <div>
                    <img alt="" src={x.imgData} />
                    <div>
                      <h3>{x.author}</h3>
                      <h4>{x.authorDescription}</h4>
                    </div>
                  </div>
                    <p>{x.comment}</p>
                  </div>
                  )
                })
              }
            </Carousel>
        </div>
      </section>
    );
  }
}

export default Comments;