const imagesDisplay = images.images.map(x => {
  return {
    original: x.url,
    thumbnail: x.url
  }
});

let toDate = (d) => {
  let date = d.split('/');
  let day = date[0];
  let month = parseInt(date[1]);
  let year = date[2];
  let months = [
    'enero',
    'febrero',
    'marzo',
    'abril',
    'mayo',
    'junio',
    'julio',
    'agosto',
    'septiembre',
    'octubre',
    'noviembre',
    'diciembre'
  ];
  month = months[month + 1];
  return `${day} de ${month}, ${year}.`;
}

class Gallery extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      images: images,
      imagesDisplay: imagesDisplay,
      imgSelect: images.images[0]
    };

    this.x = this.x.bind(this);
  }

  x(e) {
    let imgSelect = images.images.filter(x => e === x.id);

    this.setState({
      imgSelect: imgSelect[0]
    });
  }

  render() {
    return (
      <section className="main-section" id="gallery">
        <div className="gallery-container">
          <ImageGallery
            items={imagesDisplay}
            onSlide={this.x}
          />
          <GalleryDescription
            image={this.state.imgSelect}
          />
        </div>
      </section>
    );
  }
}

const GalleryDescription = (props) => {
  return (
    <div className="gallery-description">
      <h2 id='img-title'>{props.image.title}</h2>
      <p id='img-desc'>{props.image.description}</p>
      <p id='img-date'>{toDate(props.image.date)}</p>
    </div>
  );
}

export default Gallery;