import React from 'react'
import '../styles/nav.scss'

class Nav extends React.Component {
  constructor(props) {
    super(props)
    this.state = {}
  }

  render() {
    return (
      <nav id='nav'>
        <div id='nav-banner'>
          <i className='fa-solid fa-scale-balanced'></i>
          <h3>Milagro Rengifo</h3>
        </div>
        <ul id='nav-list'>
          <NavItems />
        </ul>
      </nav>
    )
  }
}

const NavItems = () => {
  let idItems = [
    ['home', 'house', 'Home'],
    ['gallery', 'image', 'Galeria'],
    ['comments', 'comments', 'Comentarios'],
    ['law-capsule', 'microphone', 'Capsula Juridica'],
    ['contacts', 'id-badge', 'Contacto']
  ]

  const navItems = idItems.map((x) => {
    return (
      <li className='nav-item' id={x[0] + '-link'}>
        <a className='nav-link' href={'#' + x[0]}>
          {x[2]}
        </a>
        <i className={'nav-link-fa fa-solid fa-' + x[1]} href={'#' + x[1]} />
      </li>
    )
  })

  return navItems
}

export default Nav
