import '../styles/contacts.scss';
import React from 'react';

class Contacts extends React.Component {
  constructor(props) {
    super(props);
    this.state = {};
  }
  render() {
    return (
      <section className="main-section" id="contacts">
        <div className="contacts-container">
          <div className="form-contact-container">
              <h3>Reciba nuestras actualizaciones!</h3>
              <p>La capsula juridica y mas!</p>
              <form action="" className="contact-form">
                <label htmlFor='name'>Nombre</label>
                <input
                  className="contact-form-input"
                  id="form-name"
                  placeholder='Nombre'
                  type="text"
                  name="name"
                />
                <label htmlFor='email'>Correo Electronico</label>
                <input
                  className="contact-form-input"
                  id="form-email"
                  placeholder='ejemplo@dominio.com'
                  type="email"
                  name="email"
                />
                <button type="submit">Enviar</button>
              </form>
          </div>
          <SocialButtons />
          <div className="contacts-banner" />
        </div>
      </section>
    );
  }
}

const SocialButtons = () => {
  const socialMedias = [
    ['facebook-f', 'http://www.hola.com'],
    ['instagram', 'http://www.hola.com'],
    ['youtube', 'http://www.hola.com'],
    ['twitter', 'http://www.hola.com'],
    ['linkedin-in', 'http://www.hola.com'],
  ];

  const socialButtons = socialMedias.map((x) => {
    return (
      <a className='social-media-button' id={x[0]} href={x[1]} target='_blank' rel='noopener noreferrer'>
        <i className={`fa-brands fa-${x[0]}`} aria-hidden="true" />
      </a>
    );
  });

  return (
    <div className="social-media-buttons">
      {socialButtons}
    </div>
  );
}

export default Contacts;