class LawCapsule extends React.Component {
  constructor(props) {
    super(props)
    this.state = {
      audios: audios,
      selectedAudio: audios.audios[0]
    }

    this.audioHandler = this.audioHandler.bind(this);
  }

  audioHandler(e) {
    let id = parseInt(e.target.id.split('audio')[1]);
    let audios = this.state.audios.audios;
    let selectedAudio = audios.filter(x => x.id === id);
    console.log(selectedAudio);
    this.setState({
      selectedAudio: selectedAudio[0]
    })
  }

  render() {
    return (
      <section className='main-section' id='law-capsule'>
        <div id='audio-track-selector'>
          <AudioBlocks
            audios={this.state.audios}
            audioHandler={this.audioHandler}
          />
        </div>
        <div id='audio-track-block'>
          <div id='audio-track-description'>
            {this.state.selectedAudio.translate}
          </div>
          <div id='audio-track-control'>
            <AudioPlayer src={this.state.selectedAudio.urldata} volume={1} />
          </div>
        </div>
      </section>
    )
  }
}

const AudioBlocks = (props) => {
  let audios = props.audios.audios.map((x) => {
    return (
      <div
        className="track-id"
        id={'track-id' + x.id}
        key={x.id}
        >
        <div
          className="track-img fa fa-play"
          id={'audio' + x.id}
          onClick={props.audioHandler}
        />
        <div className='track-list'>
          <h4 className='track-list-title' id={'title' + x.id}>{x.title}</h4>
          <p className='track-list-description' id={'description' + x.id}>{x.description}</p>
          <p className='track-upload-date' id={'time' + x.id}>{x.date}</p>
        </div>
      </div>
    );
  });

  return (audios);
}

export default LawCapsule;